@import url('https://fonts.googleapis.com/css2?family=Roboto:ital@1&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-family: 'Roboto', sans-serif;
}

.result {
    padding-top: 20px;
    border-top: 2px solid #444;
}

.result h2, .result h3 {
    font-weight: normal;
}

.result h3 {
    padding-top: 20px;
}

.result em {
    font-weight: bold;
}

.app {
    margin: 10px 20px;
}

h1 {
    padding-bottom: 20px;
    color: #444;
}

.app input {
    padding: 10px 20px;
    margin-bottom: 10px;
    border: 2px solid #444;
    background-color: #fff;
    transition: .3s;
}

.app input:hover {
    background-color: #eee;
    border-color: #000;
}